import React from 'react';
import styled from 'styled-components';
import { Body1 } from '@sberdevices/plasma-styles';
import { accent } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { CartListContainer, CartListScroller, CartTotalPrice } from 'components/Cart/CartList';

import { StatusDetailsListItem } from '../ListItem/StatusDetailsListItem';

const StyledSalePrice = styled(Body1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${accent};
`;

const StyledOldPrice = styled.span`
    text-decoration: line-through;

    opacity: 0.24;
`;

const renderPrice = (totalPrice: string, salePrice: string) => {
    if (!salePrice) {
        return `${totalPrice} ₽`;
    }

    return (
        <>
            {salePrice} ₽&nbsp;<StyledOldPrice>{totalPrice} ₽</StyledOldPrice>
        </>
    );
};

export const StatusDetailsList: React.FC = () => {
    const {
        order,
        wrapperYShift,
        topGradientVisible,
        bottomGradientVisible,
        totalPrice,
        salePrice,
        salePercent,
    } = useSelector(({ orderStatusDetailsPage }) => orderStatusDetailsPage);

    const itemList = React.useMemo(
        () =>
            order.map((itemId, index, list) => (
                <StatusDetailsListItem
                    key={itemId}
                    canFocus={index > 0 && index < list.length - 2}
                    index={index + 1}
                    id={itemId}
                />
            )),
        [order],
    );

    return (
        <CartListContainer>
            {order.length > 0 && (
                <CartListScroller
                    topGradientVisible={topGradientVisible}
                    bottomGradientVisible={bottomGradientVisible}
                    wrapperYShift={wrapperYShift}
                >
                    {itemList}
                </CartListScroller>
            )}
            <CartTotalPrice>Сумма заказа {renderPrice(totalPrice, salePrice)}</CartTotalPrice>
            {Boolean(salePercent) && <StyledSalePrice>Скидка {salePercent}% при заказе через SberBox</StyledSalePrice>}
        </CartListContainer>
    );
};
