import React from 'react';
import styled from 'styled-components';
import { Body1 } from '@sberdevices/plasma-styles';
import { warning, accent } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { CartListContainer, CartListScroller, CartTotalPrice } from 'components/Cart/CartList';

import { CartListItem } from '../ListItem/CartListItem';

const StyledMinimalPrice = styled(Body1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${warning};
`;

const StyledSalePrice = styled(Body1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${accent};
`;

const StyledOldPrice = styled.span`
    text-decoration: line-through;

    opacity: 0.24;
`;

const renderPrice = (totalPrice: string, salePrice: string) => {
    if (!salePrice) {
        return `${totalPrice} ₽`;
    }

    return (
        <>
            {salePrice} ₽&nbsp;<StyledOldPrice>{totalPrice} ₽</StyledOldPrice>
        </>
    );
};

export const CartList: React.FC = () => {
    const {
        order,
        totalPrice,
        toMinimumPrice,
        wrapperYShift,
        topGradientVisible,
        bottomGradientVisible,
        salePercent,
        salePrice,
    } = useSelector(({ cartPage }) => cartPage);

    const itemList = React.useMemo(
        () => order.map((itemId, index) => <CartListItem key={itemId} index={index + 1} id={itemId} />),
        [order],
    );

    return (
        <CartListContainer>
            {order.length > 0 && (
                <CartListScroller
                    topGradientVisible={topGradientVisible}
                    bottomGradientVisible={bottomGradientVisible}
                    wrapperYShift={wrapperYShift}
                >
                    {itemList}
                </CartListScroller>
            )}
            <CartTotalPrice>Сумма заказа {renderPrice(totalPrice, salePrice)}</CartTotalPrice>
            {Boolean(toMinimumPrice) && (
                <StyledMinimalPrice>До минимального заказа еще {toMinimumPrice} ₽</StyledMinimalPrice>
            )}
            {Boolean(!toMinimumPrice && salePercent) && (
                <StyledSalePrice>Скидка {salePercent}% при заказе через SberBox</StyledSalePrice>
            )}
        </CartListContainer>
    );
};
